* {
  margin: 0px;
  padding: 0px;
}

ul {
  list-style-type: none;
}

body {
  background-color: #f6f6f6;
}

.body {
  font-family: "DM Sans", sans-serif;
  color: #000000;
  font-size: 14px;
  font-weight: 700;
}

.home_block {
  max-width: 1440px;
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media screen and (min-width: 768px) {
  .home_block {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media screen and (min-width: 1440px) {
  .home_block {
    padding-left: 24px;
    padding-right: 24px;
  }
}

.section-shopping-list {
  max-width: 1440px;
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media screen and (min-width: 768px) {
  .section-shopping-list {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media screen and (min-width: 1440px) {
  .section-shopping-list {
    padding-left: 24px;
    padding-right: 24px;
  }
}

.charity-container {
  border-radius: 16px;
  color: #ffffff;
  background: radial-gradient(circle at top, #4f2ee8, rgba(93, 53, 202, 0.386));
  overflow: hidden;
  position: relative;
  padding: 28px 40px 20px 40px;
}

.charity-item {
  display: flex;
  align-items: center;
  margin-left: 16px;
  margin-right: 20px;
  filter: brightness(0) invert(1);
}
.charity-item p {
  display: none;
}
.charity-item img {
  height: auto;
  cursor: pointer;
  margin: 10px;
  margin-left: 20px;
  max-width: 131px;
  max-height: 32px;
}

.titl {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.herb {
  fill: #ffffff;
  width: 20px;
  height: 32px;
  margin-left: 20px;
}

.herb-container {
  margin-right: 20px;
}

.charity-slider {
  height: 292px;
  height: 292px;
  position: relative;
  margin-bottom: 72px;
}
.charity-slider .slider {
  transition: transform 0.5s ease-in-out;
  height: 100%;
}

.arrow-container {
  position: absolute;
  bottom: 32px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  cursor: pointer;
}
.arrow-container .arrow {
  width: 38px;
  height: 38px;
  fill: #4f2ee8;
  transition: transform 0.3s ease-in-out;
  position: relative;
  transform-origin: center;
  background-color: white;
  border-radius: 50%;
}
.arrow-container .arrow:hover {
  transform: scale(1.2);
}
.arrow-container .arrow:not(.up) {
  transform: scaleY(-1);
}

.container_home {
  max-width: 335px;
  max-height: 460px;
}
@media screen and (min-width: 1440px) {
  .container_home {
    max-width: 356px;
    max-height: 474px;
  }
}

.home_block {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
@media screen and (min-width: 1440px) {
  .home_block {
    flex-direction: row;
  }
}

.side {
  width: 100%;
  max-width: 335px;
}
@media screen and (min-width: 1440px) {
  .side {
    max-width: 357px;
  }
}
.home_gallery {
  display: inline-flex;
  flex-wrap: wrap;
  gap: 40px;
  width: 100%;
}

.card img {
  width: 100%;
}

button.see-more-button {
  width: 124px; /* Szerokość */
  height: 46px;
  background: #f6f6f6;
  display: flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 40px;
  border: 2px solid;
  border-color: #4f2ee8;
  margin-top: 40px;
  margin-left: auto;
  font-size: 14px;
  font-style: normal;
  line-height: 18px;
  font-weight: 700;
  letter-spacing: -0.18px;
  text-transform: uppercase;
}
button.see-more-button:hover, button.see-more-button:focus {
  background: #4f2ee8;
  color: #ffffff;
}

body {
  font-family: "DM Sans", sans-serif;
}
body .categories-container {
  margin-bottom: 40px;
  overflow-y: auto;
  width: 100%;
  max-height: 228px;
  padding: 10px;
  max-width: 335px;
}
@media screen and (min-width: 1440px) {
  body .categories-container {
    max-width: 328px;
    max-height: 472px;
    margin-bottom: 86px;
  }
}
body .category-title-container {
  background-color: #4f2ee8;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
}
body .category-title-container .category-title {
  color: white;
  font-size: 18px;
}
body .category-title-container .category-title-last-word {
  color: #4f2ee8;
}
body .category-header {
  width: 382px;
  height: 52px;
  top: 112px;
  left: 420px;
}
body .category-header-black {
  font-family: DM Sans;
  text-align: left;
  color: black;
  letter-spacing: -1.28px;
  line-height: 38px;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
}
@media screen and (min-width: 1440px) {
  body .category-header-black {
    font-size: 48px;
    line-height: 52px;
    letter-spacing: -1.92px;
  }
}
body .category-header-last-word {
  font-family: DM Sans;
  text-align: left;
  letter-spacing: -1.28px;
  line-height: 38px;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  color: #4f2ee8;
}
@media screen and (min-width: 1440px) {
  body .category-header-last-word {
    font-size: 48px;
    line-height: 52px;
    letter-spacing: -1.92px;
  }
}
body .category-item {
  margin-bottom: 32px;
  cursor: pointer;
  transition: color 0.3s ease, font-weight 0.3s ease;
}
body .category-item:hover, body .category-item:focus {
  color: #4f2ee8;
  font-weight: bold;
}
body .category-item.active {
  color: #4f2ee8;
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -2%;
}
body .categories-container::-webkit-scrollbar {
  width: 8px;
  height: 167px;
}
body .categories-container::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 12px;
}
body .categories-container::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 12px;
}
body .all-categories-header {
  margin-bottom: 10px;
  cursor: pointer;
  color: black;
}
body .books-container {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
  transition: opacity 0.5s ease;
}
body .books-container .category-title-container {
  display: none;
  margin-bottom: 20px;
}
body .books-container .category-title-container .category-title-last-word {
  color: #4f2ee8;
}
body .book-card {
  position: relative;
  text-align: center;
  max-width: 335px;
}
@media screen and (min-width: 1440px) {
  body .book-card {
    max-width: 180px;
  }
}
body .book-card .quick-view-overlay {
  position: absolute;
  overflow: hidden;
  top: 80%;
  left: 0;
  width: 100%;
  height: 20%;
  padding-top: 15px;
  border-radius: 0px 0px 8px 8px;
  text-align: center;
  background-color: #eac645;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.book-card:hover body .book-card .quick-view-overlay {
  top: -80%;
}
body .book-card .image-container {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  max-height: 485px;
}
body .book-card img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin: 0;
  max-height: 485px;
}
@media screen and (min-width: 1440px) {
  body .book-card img {
    max-height: 256px;
  }
}
body .book-card .book-details {
  margin-top: 14px;
  display: flex;
  flex-direction: column;
  align-items: baseline;
}
body .book-card .book-details h3 {
  color: #111;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  /* 112.5% */
  letter-spacing: -0.64px;
}
body .book-card .book-details p {
  color: #b4afaf;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
  /* 116.667% */
  letter-spacing: -0.48px;
}
body .popular-categories-container {
  display: none;
  margin-top: 20px;
  font-weight: bold;
}

