.page-header {
  max-width: 1440px;
  margin: auto;
  padding: 0 24px;
  height: 72px;
  border-radius: 0 0 8px 8px;
  border: 1.5px solid #111111;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  margin-bottom: 40px;
}
.page-header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.page-header__logo {
  display: flex;
  align-items: center;
}

.logo-and-nav {
  display: flex;
  gap: 92px;
}

.nav {
  align-self: center;
  display: flex;
  gap: 40px;
}

.nav__item {
  text-decoration: none;
  list-style-type: none;
  position: relative;
}

.nav__link {
  display: flex;
  gap: 4px;
  align-items: center;
  text-transform: uppercase;
  color: #000000;
  fill: #000000;
  font-size: 14px;
  font-family: DM Sans;
  font-style: normal;
  text-decoration: none;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.56px;
  padding: 8px 16px 8px 16px;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.nav__link:hover, .nav__link:active, .nav__link:focus, .nav__link:visited {
  background-color: #eac645;
  color: black;
  fill: #eac645;
  border-radius: 36px;
  padding: 8px 16px 8px 16px;
}

.nav__icon {
  color: #000000;
  list-style: none;
  position: absolute;
  top: 40%;
  transform: translateY(-40%);
  right: -20px;
  margin-left: 20px;
}

.header-content {
  display: none;
}

.mobile-menu {
  display: none;
}

.menu-toggle {
  display: none;
}

.books__img {
  display: none;
}

.header__icon__dark {
  display: none;
}

@media screen and (max-width: 768px) {
  .nav {
    display: none;
  }
}
@media (max-width: 768px) {
  .nav {
    display: none;
  }
  .menu-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(#4f2ee8, #dcdcdc);
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(100%);
  }
  .logo {
    margin-left: 24px;
  }
  .js-menu-container.is-open {
    transform: translateX(0);
  }
  .header-content {
    position: fixed;
    top: 0;
    width: 100%;
    height: 72px;
    border-radius: 0 0 8px 8px;
    border: 1.5px solid #111111;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    z-index: 1000;
    margin-left: 20px;
    margin-right: 20px;
  }
  .menu-toggle {
    display: block;
    cursor: pointer;
    padding: 0;
    margin: 0;
    background: none;
    border: none;
  }
  .menu-icon {
    display: block;
    stroke: #000000;
  }
  .menu-icon:hover {
    stroke: #4f2ee8;
  }
  .switcher {
    display: flex;
    gap: 14px;
  }
  .menu-toggle-close {
    display: block;
    fill: #000000;
    stroke: #000000;
    margin-right: 14px;
    width: 20px;
    height: 20px;
  }
  .mobile-menu {
    background-image: url(/src/images/books-mobile.png);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 380px 40px;
    flex-grow: 1;
    position: relative;
  }
  .mobile-menu::after {
    content: "";
    position: absolute;
    width: 100%; /* Ustaw pełną szerokość tła */
    height: 20px; /* Ustaw wysokość tła według potrzeb */
    bottom: 50px;
    left: 0;
    z-index: 0; /* Umieść tło za treścią mobile-menu */
  }
  .mobile__item {
    margin-bottom: 20px;
    text-align: center;
    position: relative;
    transform: translateY(-700%);
  }
  .mobile__item .mobile__link {
    font-family: "DM Sans", sans-serif;
    font-size: 14px;
    font-weight: 500;
    text-decoration: none;
    color: #ffffff;
  }
  .mobile__item .mobile__link--active {
    color: #000000;
    position: relative;
  }
  .mobile__item .mobile__link--active::after {
    font-weight: 700;
    content: "";
    position: absolute;
    bottom: -8px;
    left: 50%;
    transform: translateX(-50%);
    width: 72px;
    height: 34px;
    border: 1px solid #eac645;
    border-radius: 36px;
    background-color: #eac645;
    pointer-events: none;
    z-index: -1;
  }
  .mobile__icon {
    fill: #ffffff;
    list-style: none;
    position: absolute;
    top: 80%;
    transform: translateY(-90%);
    right: -20px;
  }
}
