#buttonScrollUp {
  border-radius: 50%;
  border: none;
  cursor: pointer;
  position: sticky;
  bottom: 140px;
  float: right;
  display: none;
}

#darkSwitch {
  display: flex;
  margin-left: auto;
}

.switch {
  width: 40px;
  height: 20px;
  position: relative;
  display: block;
  background: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.1) inset;
  border-radius: 200px;
  cursor: pointer;
}

.switch:after {
  content: "";
  width: 18px;
  height: 18px;
  position: absolute;
  top: 1px;
  left: 1px;
  background: rgb(247, 247, 247);
  border-radius: 180px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}

.switch p {
  display: none;
}

.dark {
  width: 0;
  height: 0;
  visibility: hidden;
}

.dark:checked + .switch {
  background: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.1) inset;
}

.dark:checked + .switch:after {
  right: 24px;
  transform: translateX(110%);
  background: rgb(255, 255, 255), rgb(247, 247, 247);
}

.switch,
.switch:after {
  transition: 0.3s;
}

